/*
* 需求管理 store
*/
import {observable, action} from 'mobx';
import Config from '@/config';
import {requestPost} from 'utils/api';
import Schemas from "@/utils/Schemas/requirement";

class RequirementStore {
  @observable totalCount = 0;
  @observable loading=false;
  @observable hasMore=true;
  @observable pageIndex = 1;
  @observable pageSize = 20;
  @observable status = [];  // 左侧checkbox筛选
  @observable product = "";  // 产品
  @observable all = true;  // 筛选条件 true 全部 false 我的
  @observable text = '';  // 关键字
  @observable tableData = []; // 列表数据
  @observable addFields = {   // 新增需求字段
    "title": "",
    "category": [],
    "level": "",
    "product":"",
    "demandName": "",
    "demandPhone": "",
    "demandStation": "",
    "description": "",
    "attachments": [],
    "id":""
  }
  @observable editFields = {};//编辑需求字段
  @observable analysisFields= {
    "id": "",
    "toUserId": "",
    "toUserName": "",
    "bsa": "",
    "note": "",
    "status": "",
    "record": "",
    "userId": "",
    "userName": "",
    "createTime": "",
    "isSave": false,
    "requirementId": "",
    "attachments": []
  }; // 新增分析字段
  @observable editAnalysisFields= {}; // 编辑分析字段
  @observable analysisList = []; // 分析列表
  @observable anActive = '';  // 当前选中的分析
  @observable rolesPageIndex = 1; // 关联分页
  @observable rolesPageSize = 20; // 关联分页
  @observable rolesTotalCount = 0; // 关联分页
  @observable rolesTable = []; // 关联列表
  @observable lisTable = []; // 已关联列表
  @observable num = null; // 拆分
  @observable requirementId = '' ; // 需求id
  @observable isSave = false ; // 当前分析是否保存 false 未保存 true 保存
  @observable statusActive = ''; // 当前需求状态
  @observable readVisible = false;  // 需求只读
  @observable readData = {};  // 需求只读数据
  @observable word = '';  // 分词检索

  // 保存需求
  @action
  saveRequirement(param) {
     return requestPost('saveRequirement', Config.management.saveRequirement, Schemas.requirement.saveRequirement, param)
  }

  // 保存分析
  @action
  saveAnalysis(param) {
    return requestPost('saveAnalysis', Config.management.saveAnalysis, Schemas.requirement.saveAnalysis, param)
  }
}


export default RequirementStore;



