/*
* 需求管理 编辑组件
* dangw@glodon.com
* */
import React, {Component} from 'react';
import {observer} from 'mobx-react';
import {
  Input,
  Row,
  Col,
  Form,
  Upload,
  Select,
  Button,
  Icon,
  Modal,
  message,
  Divider,
  Table,
  Pagination,
  List,
  Popconfirm
} from 'antd';
import * as mobx from 'mobx';
import {requestPost} from "@/utils/api";
import Config from "@/config";
import Schemas from "@/utils/Schemas/requirement";
import moment from 'moment';
import GlobalStore from '@/store/GlobalStore';
import AsyncSelectOld from '@/components/common/AsyncSelectOld';


const {Option} = Select;
const {TextArea} = Input;

@observer
class RequirementEdit extends Component {
  constructor(props) {
    super(props);
    this.store = this.props.store;
    this.state = {
      levelData: ['受理', '不考虑'], // 结果
      bsaData: ['B', 'S', 'A'], // BSA
      rolesModal: false, // 关联
      rolesData: ['重复于', '依赖于', '属于'], //
      selectedRowKeys: [], // 多选
      selectedRows: [], // 选项
      relation: '', //关系
    }
  }

  componentDidMount() {

  }

  handleChange = (type, e) => {
    //console.log(type, e.target.value)
    this.store.analysisFields[type] = e.target.value;
  }

  onSelChange = (type, value) => {
    //console.log(value, '11')
    this.store.analysisFields[type] = value;
    //console.log(this.store.analysisFields[type], '000')
  }

  // 保存草稿
  handleBlur = () => {
    const requirement = JSON.parse(JSON.stringify(this.store.analysisFields))
    // 如果标题为空，不自动保存
    if (requirement.title==""||requirement.title==null) {
      return false;
    }
    delete requirement.attachments;
    const param = {
      type: 1,
      analysis: requirement
    }
    this.store.saveAnalysis(param);
  }

  // 提交
  onClick = (callback) => {
    const requirement = JSON.parse(JSON.stringify(this.store.analysisFields))
    console.log(requirement, 'requirement')
    /*let str = ""
    if (requirement.title == ""||requirement.title==null) {
      str += "标题是必输项 "
      message.warn(str);
      return false;
    }*/
    delete requirement.attachments;
    const param = {
      type: 0,
      analysis: requirement
    }
    this.store.saveAnalysis(param).then((res) => {
      const data=res;
      if (data == undefined) {
        message.error('提交失败！')
      } else {
        message.success("提交成功！");
        // 提交成功之后，提交按钮变成已完成
        this.store.isSave = true; // 当前分析变为已提交状态
        if (typeof callback == "function") callback();
      }
    })
  }

  // 提交并进入下一条
  onNextClick = (e) => {
    e.preventDefault();
    let _this = this;
    _this.onClick(() => {
      _this.props.handleNext();
    });
  }

  // 切换分析列表
  checkList = (item) => {
    if (this.store.isSave == false) {
      message.warn("请先保存本次分析！")
      return false;
    }

    this.store.anActive = item.id;
    this.store.isSave = item.isSave;

    requestPost('getAnalysis', Config.management.getAnalysis, Schemas.requirement.getAnalysis, {
      id: item.id
    }).then((res) => {
      const data = res;
      this.store.analysisFields = Object.assign({},
        {
          "id": data.id,
          "toUserId": data.toUserId,
          "toUserName": data.toUserName,
          "bsa": data.bsa,
          "note": data.note,
          "status": data.status,
          "record": data.record,
          "userId": data.userId,
          "userName": data.userName,
          "createTime": data.createTime,
          "isSave": data.isSave,
          "requirementId": data.requirementId,
          "attachments": data.attachments !== null ? data.attachments.map((item, index) => {
            return Object.assign(item, {
              'uid': item.id,
              'name': item.fileName,
              'status': 'done',
              'url': item.filePath
            })
          }) : []
        }); // 分析字段

      //  获取关联
      requestPost('getAnalysisRelationPage',Config.management.getAnalysisRelationPage, Schemas.requirement.getAnalysisRelationPage, {
        analysisId: this.store.anActive, pageIndex: 1, pageSize: 50
      }).then((res) => {
        this.store.lisTable = Object.assign([], res.body);
      });
    });
  }

  // 关联链接
  handleOk = () => {
    requestPost('addAnalysisRelation',Config.management.addAnalysisRelation, Schemas.requirement.addAnalysisRelation,
      {
        requirementIds: this.state.selectedRows.map((item) => {
          return item.id
        }),
        analysisId: this.store.anActive,
        relation: this.state.relation
      }).then((res) => {
      message.success("关联成功！");
      this.setState({
        selectedRowKeys: [],
        selectedRows: [],
        relation: '',
        rolesModal: false
      }, () => {
        // 已列表
        requestPost('getAnalysisRelationPage',Config.management.getAnalysisRelationPage, Schemas.requirement.getAnalysisRelationPage, {
          analysisId: this.store.anActive, pageIndex: 1, pageSize: 50
        }).then((res) => {
          this.store.lisTable = Object.assign([], res.body);
        });
      })
    })
  }

  // 新增关联
  handleRouls = () => {
    this.setState({
      rolesModal: true
    }, () => {
      this.store.rolesPageIndex = 1;
      this.store.rolesPageSize = 20;
      this.getTableData();
    });
  }

  // 获取关联列表
  getTableData = () => {
    requestPost('getRequirementHistoryPage',Config.management.getRequirementHistoryPage, Schemas.requirement.getRequirementHistoryPage, {
      text: this.store.text,
      status: mobx.toJS(this.store.status),
      pageIndex: this.store.rolesPageIndex,
      pageSize: this.store.rolesPageSize
    }).then((res) => {
      this.store.rolesTable = Object.assign([], data.body);
      this.store.rolesPageIndex = data.pageIndex;
      this.store.rolesPageSize = data.pageSize;
      this.store.rolesTotalCount = data.totalCount;
    })
  }

  handleCancel = () => {
    this.setState({
      rolesModal: false,
      selectedRowKeys: [],
      selectedRows: [],
      relation: ''
    });
  }

  showSizeChange = async (current, pageSize) => {
    this.store.rolesPageIndex = 1;
    this.store.rolesPageSize = pageSize;
    this.getTableData();
  }

  onChange = async (pageNumber) => {
    this.store.rolesPageIndex = pageNumber;
    this.getTableData();
  }

  // 多选
  handSelect = (selectedRowKeys, selectedRows) => {
    this.setState({
      selectedRowKeys,
      selectedRows
    })
  }

  onSelect = (value) => {
    this.setState({
      relation: value
    });
  }

  cancel = () => {

  }

  // 拆分
  handSplit = () => {
    requestPost('splitRequirement',Config.management.splitRequirement, Schemas.requirement.splitRequirement, {
      num: this.store.num, requirementId: this.store.requirementId
    }).then((res) => {
      if (res == true) {
        message.success("拆分成功！")
        this.props.getTableData();
      } else {
        message.error("拆分失败！")
      }
    })
  }

  handInput = (e) => {
    this.store.num = e.target.value;
  }

  // 取消连接
  confirm = (id) => {
    requestPost('delAnalysisRelation',Config.management.delAnalysisRelation, Schemas.requirement.delAnalysisRelation, {
      id: id
    }).then((res) => {
      if (res == true) {
        message.success('取消链接成功！')
        requestPost('getAnalysisRelationPage',Config.management.getAnalysisRelationPage, Schemas.requirement.getAnalysisRelationPage, {
          analysisId: this.store.anActive, pageIndex: 1, pageSize: 50
        }).then((res) => {
          this.store.lisTable = Object.assign([], res.body);
        });
      } else {
        message.error('取消链接失败！')
      }
    })
  }

  // 新增分析
  handleAdd = () => {
    requestPost('addAnalysis',Config.management.addAnalysis, Schemas.requirement.addAnalysis, {
      requirementId: this.store.requirementId
    }).then((res) => {
      const data = res;
      this.store.anActive = data.id;  // 当前默认分析id
      this.store.isSave = data.isSave;  // 当前默认分析是否保存
      this.store.analysisFields = Object.assign({},
        {
          "id": data.id,
          "toUserId": data.toUserId,
          "toUserName": data.toUserName,
          "bsa": data.bsa,
          "note": data.note,
          "status": data.status,
          "record": data.record,
          "userId": data.userId,
          "userName": data.userName,
          "createTime": data.createTime,
          "isSave": data.isSave,
          "requirementId": data.requirementId,
          "attachments": data.attachments !== null ? data.attachments.map((item, index) => {
            return Object.assign(item, {
              'uid': item.id,
              'name': item.fileName,
              'status': 'done',
              'url': item.filePath
            })
          }) : []
        }); // 分析字段

      //  获取关联
      requestPost('getAnalysisRelationPage',Config.management.getAnalysisRelationPage, Schemas.requirement.getAnalysisRelationPage, {
        analysisId: this.store.anActive, pageIndex: 1, pageSize: 50
      }).then((res) => {
        this.store.lisTable = Object.assign([], res.body);
      });


      //成功获取分析列表
      requestPost('getAnalysisList',Config.management.getAnalysisList, Schemas.requirement.getAnalysisList, {requirementId: this.store.requirementId}).then((res) => {
        this.store.analysisList = Object.assign([], res);
      })
    });
  }

  // 点击已完成
  onFinishRequirement = async () => {
    requestPost('finishRequirement',Config.management.finishRequirement, Schemas.requirement.finishRequirement, {
      requirementId: this.store.requirementId
    }).then((res) => {
      message.success('更改需求状态为已完成！')
      this.store.statusActive = "已完成";
    })
  }

  // 文件变化
  fileChange = info => {
    const status = info.file.status;
    if (status === 'done') {
      // 上传成功
      message.success(`${info.file.name}上传成功！`);
    } else if (status === 'error') {
      message.error(`${info.file.name}上传失败！`);
    } else if (status === 'removed') {

    }
    let fileList = [...info.fileList];
    fileList = JSON.parse(JSON.stringify(fileList));
    fileList = fileList.filter((file) => {
      if (file.status === 'error') {
        return false;
      }
      return true;
    });
    fileList = fileList.map(file => {
      if (file.response) {
        file.url = file.response.body[0].fileURL;
      }
      return file;
    });
    fileList = fileList.filter((file) => {
      if (file.response) {
        return file.response.status === 'ok';
      }
      return true;
    });
    this.store.analysisFields.attachments = Object.assign([], [...fileList])
  }

  render() {
    const {levelData, bsaData, rolesModal, rolesData, selectedRows} = this.state;
    const formItemLayout = null;
    const formItemLayout1 = {
      labelCol: {span: 1},
      wrapperCol: {span: 23},
    }
    const formItemLayout2 = {
      labelCol: {span: 3},
      wrapperCol: {span: 21},
    }
    const columns = [
      {
        title: '需求',
        dataIndex: 'title',
        key: 'title'
      }
    ];
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        this.handSelect(selectedRowKeys, selectedRows);
      },
      getCheckboxProps: record => ({
        name: record.name,
      }),
    };
    const props = {
      data: {
        type: 2,    // 分析
        sourceId: this.store.anActive // 分析的id
      },
      name: 'file',
      multiple: true,
      action: Config.management.multiUpload,
      onChange: this.fileChange,
      onRemove: file => {
        return new Promise((resolve, reject) => {
          requestPost('delAttachment',Config.management.delAttachment, Schemas.requirement.delAttachment, {id: file.response.body[0].id})
          .then((res) => {
            if (res == true) {
              message.success("删除成功！")
              resolve(res);
            } else {
              message.error('删除失败！')
              resolve(res);
            }
          });
        });
      },
      fileList: mobx.toJS(this.store.analysisFields.attachments)
    }


    return (
      <div className={this.props.visible ? "container-fluid pb100" : "hidden"}>
        <div className="analysis-head">
          <div className="analysis-title">{this.store.editFields.number}</div>
          <Row>
            <Col span={12} className="analysis-desc">
              {this.store.editFields.title}
            </Col>
            <Col span={12} className={GlobalStore.requirementAnalysis ? "tr" : "hidden"}>
              <div style={{marginLeft: '16px'}}><span className="analysis-title">拆分成</span>
                <Input
                  onChange={this.handInput}
                  style={{width: '32px', margin: '0 8px'}}/><span className="analysis-title mr10">个</span>
                <Button
                  onClick={this.handSplit}
                  type="primary" ghost>执行</Button></div>
            </Col>
          </Row>
          <Row className="analysis-row" style={{marginBottom: '15px', color: "#333"}}>
            <Col span={3} className="tr">
              产品：
            </Col>
            <Col span={21}>
              {mobx.toJS(this.store.editFields.product)}
              <span className="ml20">分类： {mobx.toJS(this.store.editFields.category) && mobx.toJS(this.store.editFields.category).join(" ")}</span>
              <span className="ml20">优先级: {this.store.editFields.level} </span>
            </Col>
          </Row>
          <Row className="analysis-row" style={{marginBottom: '15px', color: "#333"}}>
            <Col span={3} className="tr">
              提报人：
            </Col>
            <Col span={21}>
              {this.store.editFields.userName}
              <span className="ml20">提报时间： {moment(this.store.editFields.createTime).format("YYYY-MM-DD HH:mm")}</span>
              <span className="ml20">反馈人: {this.store.editFields.demandName} </span>
            </Col>
          </Row>
          <Row className="analysis-row" style={{marginBottom: '15px', color: "#333"}}>
            <Col span={3} className="tr">
              反馈人手机：
            </Col>
            <Col span={21}>
              {this.store.editFields.demandPhone}
              <span className="ml20">反馈人岗位: {this.store.editFields.demandStation} </span>
            </Col>
          </Row>


          <Row className="analysis-row" style={{margin: '32px 0', color: "#333"}}>
            <Col span={3} className="tr">
              描述：
            </Col>
            <Col span={21}>
              {this.store.editFields.description}</Col>
          </Row>
          <Row className="analysis-row" style={{color: "#333"}}>
            <Col span={3} className="tr">
              附件：
            </Col>
            <Col span={21}>
              {this.store.editFields.attachments && mobx.toJS(this.store.editFields.attachments).length > 0 ?
                <List
                  size="small"
                  header={null}
                  footer={null}
                  bordered={false}
                  dataSource={mobx.toJS(this.store.editFields.attachments)}
                  renderItem={(item, index) => <List.Item className="analysis-fujian" key={index}>
                    <a target='_bank' href={item.url} title={item.name}>{item.name}</a>
                  </List.Item>}
                /> : null
              }
            </Col>
          </Row>
          <Row style={{margin: '60px 0 15px 0'}} className="clearfix">
            {GlobalStore.requirementAnalysis ?
              <Icon type="plus-square" className="analysis-add" onClick={this.handleAdd}/> : null}
            <ul className="ans-ul">
              {mobx.toJS(this.store.analysisList.map((item, index) => {
                return (<li className={this.store.anActive == item.id ? "pointer active" : "pointer"}
                            onClick={this.checkList.bind(this, item)}
                            key={'list' + index}>{item.name}<Divider type="vertical"/></li>)
              }))}</ul>
          </Row>
          <Form layout={'inline'}>
            <Row style={{marginBottom: '15px'}}>
              <Form.Item className="ant-col-24" label="分析人" style={{marginRight: '50px'}} {...formItemLayout2}>
                <span>{this.store.analysisFields.userName}</span>
              </Form.Item>
              <Form.Item label="分析时间" {...formItemLayout2} className={this.store.isSave==false?"hidden":"ant-col-24"}>
                <span>{moment(this.store.analysisFields.createTime).format("YYYY-MM-DD HH:mm")}</span>
              </Form.Item>
            </Row>
            <Row style={{marginBottom: '15px'}}>
              <Form.Item className="ant-col-24" label="分析记录" {...formItemLayout2}>
                <TextArea
                  placeholder="请输入分析内容"
                  onBlur={this.handleBlur}
                  onChange={this.handleChange.bind(this, 'record')}
                  value={this.store.analysisFields.record}
                  disabled={!GlobalStore.requirementAnalysis || this.store.isSave == true}
                  rows={4}/>
              </Form.Item>
            </Row>
            <Row className="analysis-row" style={{marginBottom: '15px', color: "#333"}}>
              <Col span={3} className="tr">
                分析结果：
              </Col>
              <Col span={21}>
                <div className="dib mr25">
                  <span>结果：</span>
                  <Select
                    placeholder="请选择..."
                    onChange={this.onSelChange.bind(this, 'status')}
                    onBlur={this.handleBlur}
                    style={{width: '150px'}}
                    value={this.store.analysisFields.status}
                    disabled={!GlobalStore.requirementAnalysis || this.store.isSave == true}
                  >
                    {levelData.map((item, index) => {
                      return (<Option key={'hjiguo' + index} value={item}>{item}</Option>)
                    })}
                  </Select></div>
                <div className="dib mr25">
                  <span>BSA：</span>
                  <Select
                    placeholder="请选择..."
                    onChange={this.onSelChange.bind(this, 'bsa')}
                    onBlur={this.handleBlur}
                    style={{width: '150px'}}
                    value={this.store.analysisFields.bsa}
                    disabled={!GlobalStore.requirementAnalysis || this.store.isSave == true}
                  >
                    {bsaData.map((item, index) => {
                      return (<Option key={'bsa' + index} value={item}>{item}</Option>)
                    })}
                  </Select></div>
                <div className="dib">
                  <span>责任人：</span>
                  <span>
                    <AsyncSelectOld
                      url={Config.authorization.getUserInfoListByRoleCode}
                      style={{ width: 150 }}
                      onChange={this.onSelChange.bind(this, 'toUserName')}
                      value={this.store.analysisFields.toUserName}
                      query={Schemas.requirement.getUserInfoListByRoleCode}
                      onBlur={this.handleBlur}
                      disabled={!GlobalStore.requirementAnalysis || this.store.isSave == true}
                    />
                  </span></div>
              </Col>
            </Row>
            <Row style={{marginTop: '30px'}}>
              <Form.Item className="ant-col-24" label="处理附件"  {...formItemLayout2}>
                <Upload.Dragger {...props}
                                disabled={!GlobalStore.requirementAnalysis || this.store.isSave == true}
                >
                  <p className="ant-upload-drag-icon">
                    <span className="require-upload"></span><span style={{color: '#8C8C8C'}}></span>拖拽文件到这里或<span
                    style={{color: '#268DFF', verticalAlign: 'top'}}>浏览</span>
                  </p>
                </Upload.Dragger>
              </Form.Item>
            </Row>
            <Row style={{margin: '30px 0'}}>
              <Form.Item className="ant-col-24" label="关联"  {...formItemLayout2}>
                <Button disabled={!GlobalStore.requirementAnalysis || this.store.isSave == true} type="dashed"
                        onClick={this.handleRouls} style={{width: '100%', height: '48px'}}>
                  <Icon type="plus"/> 从需求池中添加
                </Button>
              </Form.Item>
            </Row>
            <List
              size="small"
              header={null}
              footer={null}
              bordered={false}
              dataSource={mobx.toJS(this.store.lisTable)}
              renderItem={(item, index) => <List.Item key={index} style={{textAlign: 'center'}}>
                <span style={{color: '#595959'}} className="fl">{item.relation}</span> <span
                style={{color: '#268DFF'}}> {item.title}</span>
                <Popconfirm
                  title="您确定要取消链接吗?"
                  onConfirm={this.confirm.bind(this, item.id)}
                  onCancel={this.cancel}
                  okText="确定"
                  cancelText="取消"
                >
                  <Button size="small" className="fr" type="dashed">取消链接</Button>
                </Popconfirm>
              </List.Item>}
            />
          </Form>
        </div>
        <div className={GlobalStore.requirementAnalysis && this.store.anActive !== null ? "require-addSub" : "hidden"}>
          <div className="container-fluid">
            <Button className={this.store.isSave == false ? "fr ml10 mt25" : "hidden"}
                    onClick={this.onNextClick}
                    type="primary" disabled={!GlobalStore.requirementAnalysis}>提交并进入下一条</Button>
            {this.store.isSave == false ? <Button onClick={this.onClick} className="fr ml10 mt25" ghost type="primary"
                                                  disabled={!GlobalStore.requirementAnalysis}>提交</Button> :
              <Button disabled={this.store.statusActive == "已完成"} onClick={this.onFinishRequirement}
                      className="fr ml10 mt25" ghost type="primary">已完成</Button>}
            <div className="fr"
                 style={{
                   color: '#268DFF', fontSize: '13px'
                 }}>{this.store.isSave == false ? "提交后不可修改，请确认后提交" : "若该需求已经被实现则点击已完成，点击后需求状态为“已完成”"}</div>
          </div>
        </div>
        <Modal
          title="关联"
          visible={rolesModal}
          onCancel={this.handleCancel}
          className="analysis-table"
          footer={[
            <div className="fl">
              <span className="f12">此问题</span>
              <Select
                placeholder="请选择..."
                onChange={this.onSelect}
                onBlur={this.handleBlur}
                style={{marginLeft: '10px', width: '100px'}}
              >
                {rolesData.map((item, index) => {
                  return (<Option key={'roles' + index} value={item}>{item}</Option>)
                })}
              </Select></div>,
            <Button key="back" onClick={this.handleCancel}>
              取消
            </Button>,
            <Button key="submit" type="primary" disabled={selectedRows.length == 0} onClick={this.handleOk}>
              链接
            </Button>
          ]}
        >
          <Table columns={columns}
                 size="small"
                 rowSelection={rowSelection}
                 pagination={false}
                 dataSource={mobx.toJS(this.store.rolesTable)}/>
          <Pagination
            style={{margin: '10px', textAlign: 'center'}}
            current={this.store.rolesPageIndex}
            pageSize={this.store.rolesPageSize}
            pageSizeOptions={["20", "50", "100", "500"]}
            total={this.store.rolesTotalCount}
            showSizeChanger
            onShowSizeChange={this.showSizeChange}
            onChange={this.onChange}
            showLessItems={true}
            showQuickJumper/>
        </Modal>
      </div>
    );
  }
}

export default RequirementEdit;



